* {
    margin: 0;
    padding: 0;
}

body {
    background-image: url('https://img.freepik.com/premium-photo/wooden-empty-table-top-blurred-background_793039-31.jpg');
    background-size:1550px 800px; /* Sets the width and height of the background image */
    background-repeat: no-repeat;
    background-position: center; /* Optional: Center the background image */
    height: 100vh; /* Ensures the body takes the full viewport height */
    width: 100%; /* Ensures the body takes the full viewport width */
}

.welcome
{
    text-align: center;
    color: rgb(255, 255, 255);
    font-family: 'Times New Roman', Times, serif;
    font-size:50px;
    background-color: #000000;
    
}
.make
{
    text-align: center;
    color: rgb(250, 108, 0);
    border-color: black;
    
}
.Hope
{
    text-align: center;
    color: rgb(0, 0, 0);
}
.food
{
    text-align: center;
    color: white;
    margin-top:120px;
}
.Addbtn
{
    align-items: center;
    text-align: center;
    margin-top:10px;
    font-size: 25px;
    transform: translateY(50px);
    animation: slideUp 1s ease-out forwards;

}
.inputbox
{
    margin-left:600px;
    font-size: 25px;
    transform: translateY(50px);
    animation: slideUp 1s ease-out forwards;
}
.empty
{
    text-align: center;
    color: white;
}

h1 {
    font-size: 3rem;
    font-weight: bold;
    letter-spacing: 2px;
    opacity: 0;
    transform: translateY(50px);
    animation: slideUp 1s ease-out forwards;
}

/* Keyframe animation */
@keyframes slideUp {
    0% {
        opacity: 0;
        transform: translateY(50px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

h2{
    font-size:2rem;
    font-weight: bold;
    letter-spacing: 2px;
    opacity: 0;
    transform: translateY(50px);
    animation: slideUp 1s ease-out forwards;
}

