}

.deletebtn {
    text-align: center;
    align-items: center;
    margin-left: 880px;
    font-size: 25px;
    background-color: #000000; /* Red background for delete button */
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.deletebtn:hover {
    background-color: #d32f2f; /* Darker red on hover */
}
